'use client';

import { useEffect, useState, useRef, useContext } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { handlePostLoginRedirect } from '@/utils/auth-redirect';
import { GlobalLoadingContext } from '@/contexts/GlobalLoadingContext';

interface AuthCallbackClientProps {
  provider?: string;
}

// GlobalLoadingContextÎ•º ÏïàÏ†ÑÌïòÍ≤å ÏÇ¨Ïö©ÌïòÎäî ÌõÖ
const useSafeGlobalLoading = () => {
  const context = useContext(GlobalLoadingContext);
  if (context) {
    return context;
  }

  // GlobalLoadingProviderÍ∞Ä ÏóÜÎäî Í≤ΩÏö∞ Îπà Ìï®Ïàò Î∞òÌôò
  console.warn('GlobalLoadingProviderÍ∞Ä Ï†úÍ≥µÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§. ÎåÄÏ≤¥ Íµ¨ÌòÑÏùÑ ÏÇ¨Ïö©Ìï©ÎãàÎã§.');
  return {
    isLoading: false,
    setIsLoading: (loading: boolean) => {
      console.log('GlobalLoading ÏÉÅÌÉú:', loading);
    },
  };
};

// Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå Î°úÍ∑∏ Ï∂úÎ†•
const debugLog = (message: string, data?: any) => {
  if (process.env.NODE_ENV === 'development') {
    console.log(message, data || '');
  }
};

const debugError = (message: string, data?: any) => {
  if (process.env.NODE_ENV === 'development') {
    console.error(message, data || '');
  }
};

export default function AuthCallbackClient({
  provider,
}: AuthCallbackClientProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const { setIsLoading } = useSafeGlobalLoading();
  const [mounted, setMounted] = useState(false); // ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖò ÏôÑÎ£å ÌôïÏù∏
  const [error, setError] = useState<string | null>(null);
  const [isProcessing, setIsProcessing] = useState(true);
  
  // üîß Ï§ëÎ≥µ Ï≤òÎ¶¨ Î∞©ÏßÄ
  const processedRef = useRef(false);

  // üîß ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖò ÏôÑÎ£å ÌôïÏù∏
  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    // mountedÍ∞Ä falseÎ©¥ ÏïÑÏßÅ ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖòÏù¥ ÏôÑÎ£åÎêòÏßÄ ÏïäÏïòÏúºÎØÄÎ°ú ÎåÄÍ∏∞
    if (!mounted) return;

    // üöÄ Ï†ÑÏó≠ Î°úÎî©Î∞î ÏãúÏûë (Ïª§Ïä§ÌÖÄ Ïù¥Î≤§Ìä∏ + Ïª®ÌÖçÏä§Ìä∏ Ïù¥Ï§ë Î≥¥Ïû•)
    setIsLoading(true);
    
    // Ïª§Ïä§ÌÖÄ Ïù¥Î≤§Ìä∏Î°úÎèÑ Ï†ÑÏó≠ Î°úÎî©Î∞î ÏãúÏûë (Ïù¥Ï§ë Î≥¥Ïû•)
    try {
      const event = new CustomEvent('startGlobalLoading', { detail: { reason: 'auth-callback-client' } });
      window.dispatchEvent(event);
      debugLog('üîÑ [AuthCallback] Ï†ÑÏó≠ Î°úÎî©Î∞î ÏãúÏûë (Ïª®ÌÖçÏä§Ìä∏ + Ïù¥Î≤§Ìä∏)');
    } catch (error) {
      debugLog('üîÑ [AuthCallback] Ï†ÑÏó≠ Î°úÎî©Î∞î ÏãúÏûë (Ïª®ÌÖçÏä§Ìä∏Îßå)', error);
    }
    
    // üóëÔ∏è Ï¶âÏãú Î°úÎî©Î∞î Ï†úÍ±∞ (Ï†ÑÏó≠ Î°úÎî©Î∞îÎ°ú ÎåÄÏ≤¥)
    setTimeout(() => {
      const immediateLoadingBar = document.getElementById('oauth-loading');
      if (immediateLoadingBar) {
        debugLog('üóëÔ∏è [AuthCallback] Ï¶âÏãú Î°úÎî©Î∞î Ï†úÍ±∞ (Ï†ÑÏó≠ Î°úÎî©Î∞îÎ°ú ÎåÄÏ≤¥)');
        immediateLoadingBar.remove();
      }
    }, 100);
    
    // üîß ÏµúÏÜå Î°úÎî© ÏãúÍ∞Ñ Î≥¥Ïû• (ÏÇ¨Ïö©ÏûêÍ∞Ä Î°úÎî© ÏÉÅÌÉúÎ•º Ïù∏ÏßÄÌï† Ïàò ÏûàÎèÑÎ°ù)
    const minimumLoadingTime = 2000; // 2Ï¥àÎ°ú ÎäòÎ†§ÏÑú Ï∂©Î∂ÑÌïú Î°úÎî© Í≤ΩÌóò Ï†úÍ≥µ
    const startTime = Date.now();

    const ensureMinimumLoading = async (callback: () => void) => {
      const elapsed = Date.now() - startTime;
      const remainingTime = Math.max(0, minimumLoadingTime - elapsed);
      
      if (remainingTime > 0) {
        debugLog(`üîÑ [AuthCallback] ÏµúÏÜå Î°úÎî© ÏãúÍ∞Ñ Î≥¥Ïû•: ${remainingTime}ms ÎåÄÍ∏∞`);
        setTimeout(callback, remainingTime);
      } else {
        callback();
      }
    };

    // üö´ Ï§ëÎ≥µ Ï≤òÎ¶¨ Î∞©ÏßÄ
    if (processedRef.current) {
      return;
    }
    processedRef.current = true;
    
    debugLog('üîç [AuthCallback] OAuth ÏΩúÎ∞± Ï≤òÎ¶¨ ÏãúÏûë');

    const handleOAuthCallback = async () => {
      try {
        // OAuth ÏΩîÎìú ÌôïÏù∏
        const code = searchParams?.get('code');
        const oauthError = searchParams?.get('error');

        if (oauthError) {
          throw new Error(`OAuth Ïò§Î•ò: ${oauthError}`);
        }

        if (!code) {
          throw new Error('OAuth ÏΩîÎìúÍ∞Ä ÏóÜÏäµÎãàÎã§');
        }

        debugLog('üîê [AuthCallback] OAuth ÏΩîÎìú Î∞úÍ≤¨:', { code: code.substring(0, 10) + '...' });
        
        // Apple ÌäπÌôî ÌååÎùºÎØ∏ÌÑ∞ ÏàòÏßë
        let appleParams: Record<string, string> = {};
        if (provider === 'apple') {
          const user = searchParams?.get('user');
          const idToken = searchParams?.get('id_token');
          const state = searchParams?.get('state');
          
          if (user) appleParams.user = user;
          if (idToken) appleParams.id_token = idToken;
          if (state) appleParams.state = state;
          
          debugLog('üçé [AuthCallback] Apple ÌäπÌôî ÌååÎùºÎØ∏ÌÑ∞ ÏàòÏßë:', {
            hasUser: !!user,
            hasIdToken: !!idToken,
            hasState: !!state
          });
        }
        
        debugLog('üîß [OAuth] ÏÑúÎ≤Ñ APIÎ°ú ÌÜ†ÌÅ∞ ÍµêÌôò ÏãúÎèÑ (ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Î¨¥ÌïúÎåÄÍ∏∞ ÌöåÌîº)');
        
        // API ÎùºÏö∞Ìä∏Î•º ÌÜµÌïú ÏÑúÎ≤ÑÏÇ¨Ïù¥Îìú Ï≤òÎ¶¨ (Î™®Îì† ÌôòÍ≤Ω ÎèôÏùº)
        const apiUrl = '/api/auth/exchange-code';
        
        debugLog('üîó [AuthCallback] API URL ÏÑ§Ï†ï:', { 
          apiUrl,
          currentHost: window.location.hostname 
        });
        
        // üîß API Ìò∏Ï∂úÏóê ÌÉÄÏûÑÏïÑÏõÉ ÏÑ§Ï†ï (15Ï¥à)
        const controller = new AbortController();
        const timeoutId = setTimeout(() => controller.abort(), 15000);
        
        const response = await fetch(apiUrl, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            code,
            provider: provider || 'google',
            ...appleParams, // Apple ÌäπÌôî ÌååÎùºÎØ∏ÌÑ∞ Ìè¨Ìï®
          }),
          signal: controller.signal,
        });

        clearTimeout(timeoutId);

        if (!response.ok) {
          const errorData = await response.json().catch(() => ({ error: 'Unknown error' }));
          throw new Error(errorData.error || `Server error: ${response.status}`);
        }

        const data = await response.json();

        if (data.success) {
          debugLog('‚úÖ [AuthCallback] ÏÑúÎ≤ÑÏÇ¨Ïù¥Îìú OAuth Ïù∏Ï¶ù ÏÑ±Í≥µ');
          
          // üîß Ïø†ÌÇ§ ÎèôÍ∏∞ÌôîÏôÄ ÏÑ±Í≥µ Ï≤òÎ¶¨Î•º Î≥ëÎ†¨Î°ú Ïã§Ìñâ
          const [syncResult] = await Promise.allSettled([
            // Ïø†ÌÇ§ ÎèôÍ∏∞Ìôî (ÌÉÄÏûÑÏïÑÏõÉ 5Ï¥à)
            fetch('/api/auth/verify', {
              method: 'GET',
              credentials: 'include',
              signal: AbortSignal.timeout(5000),
            }),
            // ÏÑ±Í≥µ Ï†ïÎ≥¥ Ï†ÄÏû• (Ï¶âÏãú ÏôÑÎ£å)
            Promise.resolve().then(() => {
              if (typeof window !== 'undefined') {
                localStorage.setItem('auth_success', 'true');
                localStorage.setItem('auth_provider', provider || 'google');
                localStorage.removeItem('code_verifier');
                
                // üéØ ÏµúÍ∑º Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Îäî AuthStoreÏóêÏÑú APIÎ•º ÌÜµÌï¥ ÏûêÎèôÏúºÎ°ú Ï≤òÎ¶¨Îê©ÎãàÎã§
                // (Ï§ëÎ≥µ Ï≤òÎ¶¨ Î∞©ÏßÄÎ•º ÏúÑÌï¥ Ïó¨Í∏∞ÏÑúÎäî Ï†úÍ±∞Îê®)
                debugLog('‚ÑπÔ∏è [AuthCallback] ÏµúÍ∑º Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Îäî AuthStoreÏóêÏÑú ÏûêÎèô Ï≤òÎ¶¨Îê©ÎãàÎã§');
              }
            })
          ]);
          
          // ÎèôÍ∏∞Ìôî Í≤∞Í≥º Î°úÍπÖ (Ïã§Ìå®Ìï¥ÎèÑ ÏßÑÌñâ)
          if (syncResult.status === 'fulfilled') {
            debugLog('‚úÖ [AuthCallback] Ïø†ÌÇ§ ÎèôÍ∏∞Ìôî ÏÑ±Í≥µ');
          } else {
            debugLog('‚ö†Ô∏è [AuthCallback] Ïø†ÌÇ§ ÎèôÍ∏∞Ìôî Ïã§Ìå®, ÌïòÏßÄÎßå ÏßÑÌñâ:', syncResult.reason);
          }
          
          // üöÄ Ï¶âÏãú Î°úÎî©Î∞î Ï†úÍ±∞ÌïòÏßÄ ÏïäÍ≥† Î¶¨Îã§Ïù¥Î†âÏÖòÍπåÏßÄ Ïú†ÏßÄ
          debugLog('‚úÖ [AuthCallback] OAuth Ï≤òÎ¶¨ ÏôÑÎ£å, Î¶¨Îã§Ïù¥Î†âÏÖòÍπåÏßÄ Î°úÎî©Î∞î Ïú†ÏßÄ');
          
          // üéØ OAuth ÏÑ±Í≥µ Ïãú Ï†ÅÏ†àÌïú ÌéòÏù¥ÏßÄÎ°ú Î¶¨ÎîîÎ†âÏÖò
          localStorage.removeItem('auth_return_url'); // Í∏∞Ï°¥ URL Ï†úÍ±∞
          
          // returnTo ÌååÎùºÎØ∏ÌÑ∞ ÌôïÏù∏ Î∞è Ï†ÅÏ†àÌïú Î¶¨Îã§Ïù¥Î†âÏÖò URL Í≤∞Ï†ï
          const returnTo = searchParams?.get('returnTo');
          const returnUrl = handlePostLoginRedirect(returnTo || undefined);

          debugLog('üöÄ [AuthCallback] OAuth ÏÑ±Í≥µ ‚Üí Î¶¨ÎîîÎ†âÏÖò:', { returnTo, returnUrl });
          
          // üîß ÏµúÏÜå Î°úÎî© ÏãúÍ∞Ñ Î≥¥Ïû• ÌõÑ Î¶¨ÎîîÎ†âÏÖò
          ensureMinimumLoading(() => {
            debugLog('üîÑ [AuthCallback] ÌôïÏã§Ìïú Ïù∏Ï¶ù ÏÉÅÌÉú Î∞òÏòÅÏùÑ ÏúÑÌï¥ Í∞ïÏ†ú ÏÉàÎ°úÍ≥†Ïπ® Ïã§Ìñâ');
            // ÏÑ±Í≥µ ÏãúÏóêÎäî Î°úÎî© ÏÉÅÌÉú Ïú†ÏßÄ (Î¶¨Îã§Ïù¥Î†âÏÖò ÏôÑÎ£åÍπåÏßÄ)
            // setIsProcessing(false); // Ï†úÍ±∞: ÏÑ±Í≥µ ÏãúÏóêÎäî Î°úÎî©Î∞î Ïú†ÏßÄ
            
            // Î¶¨ÎîîÎ†âÏÖò ÏßÄÏó∞ ÏãúÍ∞ÑÏùÑ ÎäòÎ†§ÏÑú ÏÇ¨Ïö©ÏûêÍ∞Ä Î°úÎî©Î∞îÎ•º Ï∂©Î∂ÑÌûà Î≥º Ïàò ÏûàÎèÑÎ°ù Ìï®
            setTimeout(() => {
              debugLog('üöÄ [AuthCallback] Î°úÎî©Î∞î Ïú†ÏßÄÌïòÎ©¥ÏÑú Î¶¨ÎîîÎ†âÏÖò Ïã§Ìñâ');
              // Î¶¨ÎîîÎ†âÏÖò ÏßÅÏ†ÑÍπåÏßÄ Î°úÎî©Î∞î Ïú†ÏßÄ
              
              // ÌéòÏù¥ÏßÄ Ïñ∏Î°úÎìúÍ∞Ä ÏãúÏûëÎê† ÎïåÍπåÏßÄ Î°úÎî© ÏÉÅÌÉú ÌôïÏã§Ìûà Ïú†ÏßÄ
              const handleBeforeUnload = () => {
                debugLog('üì§ [AuthCallback] ÌéòÏù¥ÏßÄ Ïñ∏Î°úÎìú ÏãúÏûë - Î°úÎî©Î∞î Ïú†ÏßÄÎê®');
              };
              
              window.addEventListener('beforeunload', handleBeforeUnload);
              
              // üéØ Î¶¨Îã§Ïù¥Î†âÏÖò ÏßÅÏ†ÑÏóê Î°úÎî©Î∞î ÏÉÅÌÉú ÌôïÏù∏ (ÎÑàÎ¨¥ ÏûêÏ£º Ï≤¥ÌÅ¨ÌïòÏßÄ ÏïäÏùå)
              debugLog('üöÄ [AuthCallback] Î¶¨Îã§Ïù¥Î†âÏÖò Ï§ÄÎπÑ ÏôÑÎ£å, Î°úÎî©Î∞î Í≥ÑÏÜç Ïú†ÏßÄ');
              
              // Ïã§Ï†ú Î¶¨ÎîîÎ†âÏÖò Ïã§Ìñâ
              setTimeout(() => {
                window.removeEventListener('beforeunload', handleBeforeUnload);
                debugLog('üöÄ [AuthCallback] Î°úÎî©Î∞î Ïú†ÏßÄÌïòÎ©¥ÏÑú Î¶¨ÎîîÎ†âÏÖò Ïã§Ìñâ');
                window.location.href = returnUrl;
              }, 200);
              
              // Ï∂îÍ∞Ä Î≥¥Ìóò: 2Ï¥à ÌõÑÏóêÎèÑ Î¶¨Îã§Ïù¥Î†âÏÖò ÏïàÎêòÎ©¥ Í∞ïÏ†ú ÏÉàÎ°úÍ≥†Ïπ®
              setTimeout(() => {
                const expectedPath = new URL(returnUrl, window.location.origin).pathname;
                if (window.location.pathname !== expectedPath) {
                  debugLog('üí™ [AuthCallback] Ï∂îÍ∞Ä Î≥¥Ìóò: Í∞ïÏ†ú ÏÉàÎ°úÍ≥†Ïπ®');
                  window.removeEventListener('beforeunload', handleBeforeUnload);
                  window.location.reload();
                }
              }, 2000);
            }, 500); // 500msÎ°ú Ï§ÑÏó¨ÏÑú Îçî Îπ†Î•∏ Ï†ÑÌôò
          });
          
          return;
        }

        throw new Error(data.error || 'ÏÑúÎ≤ÑÏóêÏÑú OAuth Ï≤òÎ¶¨ Ïã§Ìå®');

      } catch (err: any) {
        debugError('‚ùå [AuthCallback] OAuth Ï≤òÎ¶¨ Ïã§Ìå®:', err);
        
        // üîß ÏóêÎü¨ ÏãúÏóêÎèÑ Ï†ÑÏó≠ Î°úÎî©Î∞î Ìï¥Ï†ú (Ïª®ÌÖçÏä§Ìä∏ + Ïù¥Î≤§Ìä∏ Ïù¥Ï§ë Î≥¥Ïû•)
        setIsLoading(false);
        
        // Ïª§Ïä§ÌÖÄ Ïù¥Î≤§Ìä∏Î°úÎèÑ Ï†ÑÏó≠ Î°úÎî©Î∞î Ìï¥Ï†ú (Ïù¥Ï§ë Î≥¥Ïû•)
        try {
          const event = new CustomEvent('stopGlobalLoading', { detail: { reason: 'auth-callback-error' } });
          window.dispatchEvent(event);
          debugLog('üóëÔ∏è [AuthCallback] ÏóêÎü¨ Î∞úÏÉù, Ï†ÑÏó≠ Î°úÎî©Î∞î Ìï¥Ï†ú (Ïª®ÌÖçÏä§Ìä∏ + Ïù¥Î≤§Ìä∏)');
        } catch (error) {
          debugLog('üóëÔ∏è [AuthCallback] ÏóêÎü¨ Î∞úÏÉù, Ï†ÑÏó≠ Î°úÎî©Î∞î Ìï¥Ï†ú (Ïª®ÌÖçÏä§Ìä∏Îßå)', error);
        }
        
        // Ï¶âÏãú Î°úÎî©Î∞îÎèÑ Ï†úÍ±∞ (ÌòπÏãú ÎÇ®ÏïÑÏûàÎã§Î©¥)
        const immediateLoadingBar = document.getElementById('oauth-loading');
        if (immediateLoadingBar) {
          immediateLoadingBar.remove();
        }
        
        ensureMinimumLoading(() => {
          setError(`Î°úÍ∑∏Ïù∏ Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§`);
          setIsProcessing(false);

          // 2Ï¥à ÌõÑ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨ÎîîÎ†âÏÖò
          setTimeout(() => {
            debugLog('üîÑ [AuthCallback] Ïò§Î•ò Î∞úÏÉù, Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨ÎîîÎ†âÏÖò');
            router.push('/ja/login');
          }, 2000);
        });
      }
    };

    handleOAuthCallback();
  }, [mounted]); // mounted ÏùòÏ°¥ÏÑ± Ï∂îÍ∞Ä

  // üîß ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖò ÎØ∏Ïä§Îß§Ïπò Î∞©ÏßÄ: ÏÑúÎ≤ÑÏôÄ ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑú ÎèôÏùºÌïú UI Î†åÎçîÎßÅ
  if (!mounted) {
    return null; // ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖò ÏôÑÎ£å Ï†ÑÏóêÎäî ÏïÑÎ¨¥Í≤ÉÎèÑ Î†åÎçîÎßÅÌïòÏßÄ ÏïäÏùå
  }

  // ÏóêÎü¨ Î∞úÏÉù Ïãú UI
  if (error) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen bg-gray-50">
        <div className="bg-white p-8 rounded-lg shadow-md max-w-md w-full text-center">
          <div className="text-red-500 text-6xl mb-4">‚ùå</div>
          <h2 className="text-xl font-semibold text-gray-800 mb-4">Î°úÍ∑∏Ïù∏ Ïã§Ìå®</h2>
          <p className="text-gray-600 mb-6">{error}</p>
          <p className="text-sm text-gray-500">Ïû†Ïãú ÌõÑ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§...</p>
        </div>
      </div>
    );
  }

  // Î°úÎî© Ï§ëÏùº ÎïåÎäî Ï†ÑÏó≠ Î°úÎî©Î∞îÎßå Î≥¥Ïó¨Ï£ºÍ≥† Ï∂îÍ∞Ä UIÎäî ÌëúÏãúÌïòÏßÄ ÏïäÏùå
  if (isProcessing) {
    return null;
  }

  return null;
}
